import React from "react";
import CssBaseline from '@mui/material/CssBaseline';

import { ToastContainer, toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';
//import './app.css';

import Login from './components/login/Login';
import Logout from './components/logout/Logout';
import NotFound from './components/not-found/NotFound';

import HomePage from './views/Home';
import CommandsPage from './views/Commands';
import ConfigurationPage from './views/Configuration';
import RoutesPage from './views/Routes';
import LogsPage from './views/Logs';
import EventsPage from './views/Events';
import MetadataPage from './views/Metadata';

import DashboardPage from './components/dashboard/Dashboard';

import {
  BrowserRouter,
  Routes,
  Route,
  useLocation,
  Navigate,
} from "react-router-dom";

import { Provider } from 'use-http';

import { useAuth } from './context/basicAuthContext';

function RequireAuth({ children }) {
  const auth = useAuth();
  const adminSecret = auth.token;
  
  const location = useLocation();
  if (adminSecret === '' || adminSecret === null || typeof adminSecret === 'undefined') {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }
  return children;
}

function App() {
  const auth = useAuth();

  const options = {
    persist: false, // Cache should not persist!!!
    cachePolicy: 'no-cache',
    headers: {
      Accept: 'application/json',
      'x-hlambda-admin-secret': auth?.token,
    },
    interceptors: {
      // every time we make an http request, this will run 1st before the request is made
      // url, path and route are supplied to the interceptor
      // request options can be modified and must be returned
      request: async ({ options, url, path, route }) => {
        // console.log(auth?.token);
        options.headers['x-hlambda-admin-secret'] = auth?.token;
        return options
      },
      // // every time we make an http request, before getting the response back, this will run
      // response: async ({ response }) => {
      //   const res = response
      //   if (res.data) res.data = toCamel(res.data)
      //   return res
      // }
    }
  }

  return (
    <Provider url={process.env.REACT_APP_API_URL} options={options}>
      <BrowserRouter
        // basename={'/console'}
      >
        <CssBaseline />
          <Routes>
            <Route path="/login" element={<Login/>} />
            <Route path="/logout" element={<Logout/>} />

            <Route index path="/" element={
              <RequireAuth>
                <HomePage/>
              </RequireAuth>
            } />
            <Route path="/commands" element={
              <RequireAuth>
                <CommandsPage/>
              </RequireAuth>
            } />
            <Route path="/configurations" element={
              <RequireAuth>
                <ConfigurationPage/>
              </RequireAuth>
            } />
            <Route path="/routes" element={
              <RequireAuth>
                <RoutesPage/>
              </RequireAuth>
            } />
            <Route path="/logs" element={
              <RequireAuth>
                <LogsPage/>
              </RequireAuth>
            } />
            <Route path="/events" element={
              <RequireAuth>
                <EventsPage/>
              </RequireAuth>
            } />
            <Route path="/metadata" element={
              <RequireAuth>
                <MetadataPage/>
              </RequireAuth>
            } />
            <Route path="*" element={<NotFound/>}/>
          </Routes>
        <ToastContainer theme="dark" />
      </BrowserRouter>
    </Provider>
  );
}

export default App;
