import React from "react";

import useLocalStorage from './../hooks/useLocalStorage';

// ---

export const AuthContext = React.createContext();

export function AuthProvider({ children }) {
  // const [token, setToken] = React.useState('zz');
  const [token, setToken] = useLocalStorage(process.env.REACT_APP_TOKEN_NAME, '');

  const signin = async (newToken, callback) => {
    await setToken(newToken);
    console.log(token, newToken);
    console.log('signin');
    // return basicAuthProvider.signin(() => {
    //   setToken(newToken);
    //   callback();
    // });
  };

  const signout = async (callback) => {
    await setToken('');
    console.log('signout');
    // return basicAuthProvider.signout(() => {
    //   setToken(null);
    //   callback();
    // });
  };

  const value = { token, signin, signout };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export function useAuth() {
  return React.useContext(AuthContext);
}
