import React from "react";
import Container from '@mui/material/Container';

import TopBar from './../components/top-bar';
import ConsoleEditor from './../components/editor/Editor';

import useFetch from 'use-http';

import Terminal from 'react-bash';

export const clear = {
    exec: ({ structure, history, cwd }, command) => {
        return { structure, cwd, history: [] };
    },
};

function Home() {
  const { get, post, response, loading, error } = useFetch()

  const extensions = { clear };
  const history = [{ value: 'Welcome to the terminal!' }];
  const structure = {
    public: {
        file1: { content: 'The is the content for file1 in the <public> directory.' },
        file2: { content: 'The is the content for file2 in the <public> directory.' },
        file3: { content: 'The is the content for file3 in the <public> directory.' },
    },
    'README.md': { content: 'Some readme' },
  };

  return (
  <>
    <TopBar/>
    <Container>
      <Terminal 
        history={history}
        structure={structure}
        extensions={extensions}
        prefix={"user@HOMEPC"}
      />
    </Container>
  </>
  )
}

export default Home;
