import React from "react";
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import ControlPointIcon from '@mui/icons-material/ControlPoint';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';

import TopBar from './../components/top-bar';

import useFetch from 'use-http';

import { toast } from 'react-toastify';

import useLocalStorage from './../hooks/useLocalStorage';

import AnsiUp from 'ansi_up';

const StyledPreCodeTag = styled('pre')(
  ({ theme }) => `
  background-color: #000;
  color: #FFF;
  overflow-y: auto;
  padding: 0;
  height: 100vh;
  display: block;
  margin: 0;
`,);


function Commands() {
  const [adminSecret, setAdminSecret] = useLocalStorage(process.env.REACT_APP_TOKEN_NAME, '');

  const { get, post, response, loading, error } = useFetch()
  const [ logs, setLogs ] = React.useState('');

  const getLogs = async () => {
    // Do things...
    const results = await get('/console/api/v1/logs?type=text');
    if (response.ok) {
      // toast.success('yeey');
      const ansi_up = new AnsiUp();
      const html = ansi_up.ansi_to_html(results)
      setLogs(html);
    } else {
      toast.error('Request errored out...')
      setLogs(JSON.stringify(results));
    }
  };

  React.useEffect(() => {
    getLogs();
  }, []);

  return (
  <>
    <TopBar/>
    <Container>
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Avatar sx={{ m: 1 }}>
          <ControlPointIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Command pannel!
        </Typography>
        <Box sx={{ marginTop: '1rem', display: 'flex' }}>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
            onClick={()=>{
              getLogs()
            }}
          >
            RELOAD LOGS!
          </Button>
        </Box>
        <Box sx={{ marginTop: '1rem', display: 'flex' }}>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
            onClick={async ()=>{
              // Do things...
              const results = await get('/console/api/v1/trigger-restart');
              if (response.ok) {
                toast.success('yeey');
              } else {
                toast.error('... meh...')
              }
            }}
          >
            RELOAD SERVER!
          </Button>
        </Box>
      </Box>
      <StyledPreCodeTag dangerouslySetInnerHTML={{ __html: loading ? 'Loading...' : logs }}></StyledPreCodeTag>
    </Container>
  </>
  )
}

export default Commands;
